import React from 'react';
import { Map, GoogleApiWrapper, Marker } from 'google-maps-react';
import mapStyles from './style';

const MyMap = props => {
  const { google } = props;
  return (
    <Map styles={mapStyles} google={google} zoom={5} initialCenter={{ lat: 51.507351, lng: -0.127758 }}>
      <Marker position={{ lat: 51.507351, lng: -0.127758 }} onClick={() => console.log('you clicked on London')} />
    </Map>
  );
};

export default GoogleApiWrapper({
  apiKey: 'AIzaSyBKTF6LFzHKK79h9aGhhh0Hkv72LX2ejcA',
})(MyMap);
